.Product {
  display: flex;
  padding: 30px;
}
.leftPane {
  width: 40%;
}
.leftPane img {
  width: 100%;
  margin-top: 50px;
}
.rightPane {
  width: 60%;
  text-align: left;
  box-sizing: border-box;
  margin-top: 40px;
  padding-left: 30px;
}

.rightPane h1 {
  font-weight: 500;
  font-size: 44px;
  letter-spacing: -2px;
}

.rightPane h3 {
  margin-top: 20px;
  font-weight: 300;
  font-size: 20px;
  letter-spacing: 1px;
  color: rgb(27, 25, 25);
}

.rightPane h3 span {
  color: #009688;
  font-weight: bold;
}

.description p {
  margin-top: 20px;
  font-size: 16px;
  font-weight: 300;
  color: #86939e;
}

.photos {
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
  margin-top: 20px;
}
.photo {
  margin: 5px;
  width: 15%;
  height: 30%;
  cursor: pointer;
  padding: 4px;
  border-radius: 6px;
}
.btn button {
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  padding: 10px 20px;
  letter-spacing: 2px;
  background-color: #009688;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  box-shadow: 0 2px 10px rgb(0 0 0 / 50%);
  margin-top: 40px;
}
